const responseInterceptors = [
    {
        name: 'formatResponse',
        success(response) {
            return response.data;
        }
    },
    {
        name: 'handleError',
        success(response) {
            if (response.code === 7004) {
                window.location.href = '/';
            }
            return response;
        }
    }
];

const requestInterceptors = [
    {
        name: 'addHttpRequestHeader',
        success(config) {
            config.headers['Authorization'] = `Bearer ${window.localStorage.getItem('person-app-token')}`;
            return config;
        },
        fail(error) {
            console.error(error);
            return Promise.reject(error);
        }
    }
];

const interceptors = {
    response: responseInterceptors,
    request: requestInterceptors,
};

function doInstall(instace, options = {}) {
    const { type } = options;
    interceptors[type].forEach(interceptor => {
        const { success, fail } = interceptor;
        instace.interceptors[type].use(success, fail);
    });
};

export function install(instace, options = {}) {
    doInstall(instace, { type: 'response' });
    doInstall(instace, { type: 'request' });

}