import { useState, useEffect } from 'react'
import { Input, Form, Row, Col, Button, Select } from 'antd'
import './index.module.less';
const time = 10;
const InputItem = (props) => {
    const { name, onClick, rules, ...rest } = props;
    const [timing, setTiming] = useState(false);
    const [count, setCount] = useState(time);
    const handleClick = () => {
        onClick();
        setTiming(true);
    }
    useEffect(() => {
        let interval = 0;
        if (timing) {
            interval = setInterval(() => {
                setCount((preScond) => {
                    if (preScond <= 1) {
                        setTiming(false);
                        clearInterval(interval);
                        return time;
                    } else {
                        return preScond - 1;
                    }
                });
            }, 1000);
        }
        return () => clearInterval(interval);
    }, [timing]);
    if (name === 'captcha') {
        return <Form.Item name={name} rules={rules}>
            <Row>
                <Col span={15}><Input  {...rest} /></Col>
                <Col span={1} />
                <Col span={8}>
                    <Button
                        style={{ width: '100%' }}
                        disabled={timing}
                        type='primary'
                        size='large'
                        onClick={handleClick}>
                        {timing ? `${count}秒` : '获取验证码'}
                    </Button>
                </Col>
            </Row>
        </Form.Item>
    }

    if (name === 'mobile') {

        const prefixSelector = (
            <Form.Item name="prefix" noStyle >
                <Select style={{ width: 70 }}>
                    <Select.Option value="86">+86</Select.Option>
                    <Select.Option value="87">+87</Select.Option>
                </Select>
            </Form.Item>
        );

        return <Form.Item name={name} rules={rules}><Input addonBefore={prefixSelector}  {...rest} /></Form.Item>
    }

    return <Form.Item name={name} rules={rules}><Input   {...rest} /></Form.Item>
};

export default InputItem;

