import React, { useState } from 'react'
import { Link, } from 'react-router-dom'
import { Form, Popover, Progress, Row, Col } from 'antd';
import InputItem from '../../components/InputItem'
import { LockOutlined, MailOutlined, UserOutlined } from '@ant-design/icons'
import SubmitButton from '../../components/SubmitButton'
import { useDispatch } from 'redux-react-hook';
import { getCaptcha, register } from '../../actions/account';
import styles from './index.module.less'

const passwordProgressMap = {
    ok: 'success',
    pass: 'normal',
    poor: 'exception'
};


const passwordStatesMap = {
    ok: (
        <div className={styles.success}>强度：强</div>
    ),
    pass: (
        <div className={styles.normal}>强度：中</div>
    ),
    poor: (
        <div className={styles.exception}>强度：弱</div>
    ),
};

const Register = () => {
    const [form] = Form.useForm();
    const handleOnfish = (values) => {
        setRegisteBtnState(true);
        const { username, password, email, captcha } = values;
        dispatch(register({ username, password, email, captcha })).then(
            setRegisteBtnState(false)
        );
    }
    const [visible, setVisible] = useState(false);
    const [popover, setPopover] = useState(false);
    const [registerBtnState, setRegisteBtnState] = useState(false);
    const getProgressState = () => {
        const value = form.getFieldValue('password');
        if (!value) return;
        if (value.length > 9) return 'ok';
        if (value.length > 5) return 'pass';
        return 'poor';
    }
    const checkConfirm = (_, value) => {
        const promise = Promise;
        if (!value) {
            return promise.reject('确认密码不能为空');
        }
        if (value && value !== form.getFieldValue('password')) {
            return promise.reject('两次输入密码不一致');
        }
        return promise.resolve();
    }

    const checkPassword = (_, value) => {
        const promise = Promise;
        if (!value) {
            setVisible(!!value);
            return promise.reject('请输入密码');
        }
        if (!visible) {
            setVisible(!!value);
        }
        setPopover(!popover);
        // if (value && form.getFieldValue('confirm')) {
        //     form.validateFields(['confirm']);
        // }
        return promise.resolve();
    }

    const dispatch = useDispatch();
    const handleClick = () => {
        form.validateFields(['username', 'email', 'password']).then(() => {
            dispatch(getCaptcha(form.getFieldsValue(['username', 'email', 'password'])));
        });
    }

    const renderProgressPassword = () => {
        const value = form.getFieldValue('password');
        const passwordState = getProgressState();
        return value && value.length && (
            <div>
                {passwordStatesMap[passwordState]}
                <Progress
                    status={passwordProgressMap[passwordState]}
                    strokeWidth={6}
                    percent={value.length * 10 > 100 ? 100 : value.length * 10}
                    showInfo={false}
                />
            </div>
        );
    }

    return <div className={styles.registerContainer}>
        <div className={styles.register}>
            <Form
                form={form}
                onFinish={handleOnfish}
                initialValues={{
                    prefix: '86',
                }}
            >
                <InputItem
                    name='username'
                    prefix={<UserOutlined style={{ color: '#1890ff' }} />}
                    placeholder='请输入用户名' size='large'
                    rules={[{ required: true, message: '请输入用户名' }, { message: '请输入正确的邮箱格式' }]}
                />
                <InputItem
                    name='email'
                    prefix={<MailOutlined style={{ color: '#1890ff' }} />}
                    placeholder='请输入邮箱' size='large'
                    rules={[{ required: true, message: '请输入用户名' }, { type: 'email', message: '请输入正确的邮箱格式' }]}
                />
                <Popover
                    content={renderProgressPassword}
                    overlayStyle={{ width: 240 }}
                    visible={visible}
                    placement="right"
                >
                    <InputItem
                        name='password'
                        type='password'
                        autoComplete="off"
                        prefix={<LockOutlined style={{ color: '#1890ff' }} />}
                        placeholder='请输入密码' size='large'
                        rules={[{ validator: checkPassword }]}
                    />
                </Popover>
                <InputItem
                    name='confirm'
                    type='password'
                    prefix={<LockOutlined style={{ color: '#1890ff' }} />}
                    placeholder='请确认密码' size='large'
                    rules={[{ validator: checkConfirm }]}
                />
                <InputItem
                    name='mobile'
                    type='mobile'
                    placeholder='请输入手机号' size='large'
                    rules={[{ required: true, message: '手机号不能为空' }, { pattern: /^\d{11}$/, message: '请输入正确的手机号' }]}
                />
                <InputItem
                    name='captcha'
                    prefix={<MailOutlined style={{ color: '#1890ff' }} />}
                    placeholder='请输入验证码' size='large'
                    onClick={handleClick}
                    rules={[{ required: true, message: '请输入密码' }]}
                />
                <Row justify="space-between">
                    <Col span={12}>
                        <SubmitButton loading={registerBtnState} >注册</SubmitButton>
                    </Col>
                    <Col span={6} style={{ lineHeight: '40px' }}>
                        <Link to='/login' className={styles.link}>已有账号登陆</Link>
                    </Col>
                </Row>
            </Form>
        </div>
    </div>
};

export default Register;