import * as api from '../api/account'
import { message as messageNotice } from 'antd';

export function getCaptcha(payload = {}) {
    return async () => {
        const result = await api.getCaptcha(payload);
        const { code, message } = result.data;
        if (code === 20020) {
            messageNotice.success(message);
        } else {
            messageNotice.error(message);
        }
    }
}

export function register(payload = {}) {
    return async () => {
        const result = await api.register(payload);
        const { code, message } = result;
        if (code === 20023) {
            messageNotice.success(message).then(
                window.location.href = '/login'
            );;
        } else {
            messageNotice.error(message);
        }
    }
}

export function login(payload = {}) {
    return async () => {
        const result = await api.login(payload);
        const { code, message, data = {} } = result;
        const { token } = data;
        if (code === 0) {
            messageNotice.success(message).then(
                window.localStorage.setItem('person-app-token', token),
                window.location.href = '/'
            );
        } else {
            messageNotice.error(message);
        }
    }
}